<template>
<div>

    <transition name="fade">
        <div class="menu menu--mod" v-if="memorial && showHamburger">
            <div class="menu__top">
                <img class="menu__humburger" src="~@/assets/img/icon/humburger-close.svg"
                     @click.prevent="showHamburger = false"
                >
                <div class="menu__header" v-html="memorial.name">
                </div>
                <div class="menu__empty"></div>
            </div>

            <div class="menu__middle menu__middle--mod">
                <router-link :to="{name: 'home'}" class="menu__link menu__link--mod">{{
                    $ml.get('memorial.header.toHome') }}
                </router-link>
                <router-link :to="{ name: 'memorials.life', params: { slug: this.memorial.slug } }"
                             class="menu__link menu__link--mod"
                >{{ $ml.get('memorial.header.life') }}
                </router-link>
                <router-link :to="{ name: 'memorials.relatives', params: { slug: this.memorial.slug } }"
                             class="menu__link menu__link--mod active"
                >{{ $ml.get('memorial.header.relatives') }}
                </router-link>
            </div>

            <div class="menu__bottom">
                <div class="menu__title">{{ $ml.get('menu.contacts') }}</div>
                <div class="menu__conection">
                    <div class="menu__conection-item">
                        <a class="menu__relink" :href="$ml.get('contacts.whatsapp.url')">
                            <img src="~@/assets/img/icon/menu-whatsap.svg" alt="">
                        </a>
                        <span>{{ $ml.get('contacts.whatsapp.text') }}</span>
                    </div>
                    <div class="menu__conection-item">
                        <a class="menu__relink" :href="$ml.get('contacts.phone.url')">
                            <img src="~@/assets/img/icon/menu-phone.svg" alt="">
                        </a>
                        <span>{{ $ml.get('contacts.phone.text') }}</span>

                    </div>
                    <div class="menu__conection-item">
                        <a class="menu__relink" :href="$ml.get('contacts.email.url')">
                            <img src="~@/assets/img/icon/menu-envelope.svg" alt="">
                        </a>
                        <span>{{ $ml.get('contacts.email.text') }}</span>
                    </div>
                </div>
            </div>
        </div>
    </transition>

    <HelpCenter
      v-if="memorial"
      :withStoryBtn="true"
      :memorialSlug="$store.getters['auth/isLoggedIn'](['customer']) ? memorial.id : memorial.slug"
    />

    <div class="relatives-wrapper">
        <section class="relatives-header" v-if="memorial">
            <!--    100   .fixed-header--scroll -->
            <div class="fixed-header life-main-header" :class="{'fixed-header--scroll': stickyHeader}">
                <div class="fixed-header__container">
                    <img class="header-screen-hamburger" src="~@/assets/img/icon/hamburger.svg" alt=""
                         @click.prevent="showHamburger = true">
                    <div class="fixed-header__box">
                        <div class="fixed-header__link-wrapper">
                            <span class="fixed-header__link fixed-header__link--bigger" v-html="memorial.name"></span>
                            <router-link :to="{ name: 'home' }" class="fixed-header__link-sub">
                                <svg xmlns="http://www.w3.org/2000/svg" width="9.792" height="15.857"
                                     viewBox="0 0 9.792 15.857">
                                    <path id="Path_1413" data-name="Path 1413"
                                          d="M9.792,13.994,3.74,7.929,9.792,1.863,7.929,0,0,7.929l7.929,7.929Z"
                                          fill="currentColor"/>
                                </svg>
                                {{ $ml.get('memorial.header.toHome') }}
                            </router-link>
                        </div>
                        <router-link :to="{ name: 'memorials.life', params: { slug: this.memorial.slug } }"
                                     class="fixed-header__link fixed-header__life-story fixed-header__link--margin"
                        >{{ $ml.get('memorial.header.life') }}
                        </router-link>
                        <router-link :to="{ name: 'memorials.relatives', params: { slug: this.memorial.slug } }"
                                     class="fixed-header__link active"
                        >{{ $ml.get('memorial.header.relatives') }}
                        </router-link>
                    </div>
                    <div class="fixed-header__logo">
                        <router-link :to="{ name: 'home' }">
                            <img v-if="!stickyHeader" class="card__logo" src="~@/assets/img/logo-white.svg" alt="Logo">
                            <img v-if="stickyHeader" class="card__logo" src="~@/assets/img/logo.svg" alt="Logo">
                        </router-link>
                    </div>
                </div>
            </div>
            <div class="second-container  ">
                <div class="relatives-header__container">

                    <!-- floating-header -->

                    <div class="floating-header" :class="{'float': stickyHeader}">
                        <img class="header-screen-hamburger" src="~@/assets/img/icon/hamburger.svg" alt=""
                             @click.prevent="showHamburger = true">
                        <div class="floating-header__wrapper">
                            <span class="floating-header__link" v-html="memorial.name"></span>
                            <router-link :to="{ name: 'home' }" class="floating-header__header-sub">
                                <svg xmlns="http://www.w3.org/2000/svg"
                                     width="9.792" height="15.857" viewBox="0 0 9.792 15.857">
                                    <path id="Path_1413" data-name="Path 1413"
                                          d="M9.792,13.994,3.74,7.929,9.792,1.863,7.929,0,0,7.929l7.929,7.929Z" fill="currentColor">
                                    </path>
                                </svg>
                                {{ $ml.get('memorial.header.toHome') }}
                            </router-link>
                        </div>
                        <div class="floating-header__logo">
                            <router-link :to="{ name: 'home' }">
                                <img v-if="!stickyHeader" class="card__logo" src="~@/assets/img/logo-white.svg" alt="Logo">
                                <img v-if="stickyHeader" class="card__logo" src="~@/assets/img/logo.svg" alt="Logo">
                            </router-link>
                        </div>
                    </div>

                    <h1 class="relatives-header__title first-header">
                        {{ $ml.get('relatives.title') }}
                    </h1>
                </div>
            </div>
        </section>

        <!-- relatives-slider -->
        <section class="relatives-slider" v-if="memorial && (memorial.relations.length > 1 || (storiesFormated && storiesFormated.length))">
            <div class="second-container">
                <!-- relatives-header__slider -->
                <div class="relatives-slider__slider">
                    <div class="swiper-container">
                        <div class="swiper-wrapper">
                            <!-- swiper-slide relatives-slider__slide 1-->
                            <div v-for="relative in memorial.relations" class="swiper-slide relatives-slider__slide"
                                @click="selectedRelative(relative.user)">
                                <div class="relatives-slider__slide-img">
                                    <div class="relatives-slider__slide-img-wrapper">
                                        <img :src="relative.user.profilePhoto ? relative.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')"
                                             :alt="relative.user.name">
                                    </div>
                                    <div class="relatives-slider__slide-name">{{ relative.user.name }}</div>
                                    <div class="relatives-slider__slide-relations">{{ relative.relationToMemorialPerson
                                        }}
                                    </div>
                                </div>
                            </div>

                        </div>

                        <div>
                            <div class="swiper-button-prev swiper-button" slot="button-prev" @click="prevSlide()">
                                <svg xmlns="http://www.w3.org/2000/svg" width="18.787" height="18.787"
                                     viewBox="0 0 18.787 18.787">
                                    <path id="Path_1969" data-name="Path 1969"
                                          d="M22.787,12.219H8.5l6.564-6.564L13.393,4,4,13.393l9.393,9.393,1.656-1.656L8.5,14.568h14.29Z"
                                          transform="translate(-4 -4)" fill="currentColor"/>
                                </svg>
                            </div>
                            <div class="swiper-button-next swiper-button" slot="button-next" @click="nextSlide()">
                                <svg xmlns="http://www.w3.org/2000/svg" width="18.787" height="18.787"
                                     viewBox="0 0 18.787 18.787">
                                    <path id="Path_1969" data-name="Path 1969"
                                          d="M18.787,8.219H4.5l6.564-6.564L9.393,0,0,9.393l9.393,9.393,1.656-1.656L4.5,10.568h14.29Z"
                                          fill="currentColor"/>
                                </svg>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section class="relatives-slider" v-else>
            <div class="second-container">
                <!-- relatives-header__slider -->
                <div class="relatives-slider__slider">
                    <div class="swiper-container">
                        <div class="swiper-wrapper">
                            <div v-for="i in 10" class="swiper-slide relatives-slider__slide relatives-slider__slide-mod">
                                <div class="relatives-slider__slide-img">
                                    <div class="relatives-slider__slide-img-wrapper">
                                        <img class="relatives-slider__slide-img-1" src="~@/assets/img/horin.png" alt="">

                                        <img class="relatives-slider__slide-img-2" src="~@/assets/img/icon/relatives-face.svg" alt="">
                                    </div>
                                    <div class="relatives-slider__slide-name-2"></div>
                                    <div class="relatives-slider__slide-relations-2"></div>
                                </div>
                            </div>
                        </div>

                        <div>
                            <div class="swiper-button-prev swiper-button" slot="button-prev" @click="prevSlide()">
                                <svg xmlns="http://www.w3.org/2000/svg" width="18.787" height="18.787"
                                     viewBox="0 0 18.787 18.787">
                                    <path id="Path_1969" data-name="Path 1969"
                                          d="M22.787,12.219H8.5l6.564-6.564L13.393,4,4,13.393l9.393,9.393,1.656-1.656L8.5,14.568h14.29Z"
                                          transform="translate(-4 -4)" fill="currentColor"/>
                                </svg>
                            </div>
                            <div class="swiper-button-next swiper-button" slot="button-next" @click="nextSlide()">
                                <svg xmlns="http://www.w3.org/2000/svg" width="18.787" height="18.787"
                                     viewBox="0 0 18.787 18.787">
                                    <path id="Path_1969" data-name="Path 1969"
                                          d="M18.787,8.219H4.5l6.564-6.564L9.393,0,0,9.393l9.393,9.393,1.656-1.656L4.5,10.568h14.29Z"
                                          fill="currentColor"/>
                                </svg>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

        <section class="posts" v-if="memorial && (memorial.relations.length > 1 || (storiesFormated && storiesFormated.length))">
            <div class="second-container posts__container">
                <!-- post -->
                <div class="post" v-for="(story, key) in storiesFormated" :key="story.id">
                    <div class="post__wrapper">

                        <div class="post__data">{{ story.createdAtFormat }}</div>
                        <div class="post__period">
                            <img src="~@/assets/img/icon/post-icon.svg" alt=""><span v-html="story.period.name"></span>
                        </div>
                        <p class="post__text" v-html="story.description">
                        </p>
                    </div>

                    <div v-for="(item, key) in story.content">
                        <ImageStory v-if="item.contentType == 'image'" :addClass="'post__img-box'" :item="item"
                                    :key="'image-'+item.id"/>
                        <ImageStory v-else-if="item.type == 'link'" :addClass="'post__img-box'" :item="item"
                                    :key="'image-'+item.id"/>
                        <div v-else-if="item.contentType == 'audio'"
                             style="margin-top: 70px;margin-bottom: 98px;"
                        >
                            <AudioStory
                                        :item="item"
                                        :owner="story.owner"
                                        :key="'audio-'+item.id"
                                        :addClass="['post__qoute-audio']"
                                        :addClassPlayer="['post__audio-player']"
                            />
                        </div>
                        <VideoStory v-else-if="item.contentType == 'video'" :addClass="'post__img-box'" :item="item" :key="'video-'+item.id"/>
                    </div>
                    <div v-if="key != (stories.length - 1)" class="post__decor"></div>
                </div>
            </div>
        </section>
        <section class="posts" v-else>
            <div class="second-container posts__container">
                <div class="relatives-placeholder">
                    <div class="relatives-placeholder__wrapper">

                        <div class="relatives-placeholder__common relatives-placeholder__data"></div>
                        <div class="relatives-placeholder__common relatives-placeholder__period">

                        </div>
                        <div class="relatives-placeholder__common relatives-placeholder__text relatives-placeholder__text-1"></div>
                        <div class="relatives-placeholder__common relatives-placeholder__text relatives-placeholder__text-2"></div>
                        <div class="relatives-placeholder__common relatives-placeholder__text relatives-placeholder__text-3"></div>
                    </div>

                    <div class="relatives-placeholder__common relatives-placeholder__img-box">
                    </div>
                </div>
            </div>
        </section>

        <!-- page-footer -->
        <section class="page-footer">
            <div class="container">
                <div class="page-footer__title-wrapper">
                    <div class="page-footer__logo">
                        <img src="~@/assets/img/icon/footer-logo.svg" alt="">
                    </div>
                    <p class="page-footer__title-text">
                        {{ $ml.get('memorial.footer.text') }}
                    </p>
                </div>
                <div class="page-footer__bottom">
                    <div class="page-footer__privacy-box">
                        <a :href="`./files/${$ml.get(`footer.link1File`)}`"><img
                                src="~@/assets/img/icon/footer-flag.svg" alt="">{{ $ml.get(`memorial.footer.link1`)
                            }}</a>
                        <a :href="`./files/${$ml.get(`footer.link2File`)}`">{{ $ml.get(`memorial.footer.link2`) }}</a>
                        <a href="#">{{ $ml.get(`memorial.footer.link3`) }}</a>
                    </div>
                    <div class="page-footer__btn-box">
                        <a href="https://eternity.co.il/about-us" target="_blank" class="page-footer__btn">{{
                            $ml.get('memorial.footer.button1') }}</a>
                        <a href="" class="page-footer__btn" @click.prevent.stop="openHelp">{{ $ml.get('memorial.footer.button2') }}</a>
                    </div>
                    <div class="page-footer__rights">
                        {{ $ml.get('memorial.footer.copy') }}
                    </div>
                </div>
            </div>
        </section>
    </div>
</div>
</template>

<script>
    import {mapGetters} from "vuex";

    // core version + navigation, pagination modules:
    import Swiper, {
        Navigation
    } from 'swiper';
    import 'swiper/swiper-bundle.min.css';
    import VuePlyr from 'vue-plyr';
    import 'vue-plyr/dist/vue-plyr.css';

    import HelpCenter from '@/components/HelpCenter.vue';
    import ImageStory from "@/components/storiesFull/ImageStory";
    import AudioStory from "@/components/stories/AudioStory";
    import VideoStory from "@/components/storiesFull/VideoStory";
    import { EventBus } from '@/utils/EventBus.js';

    // configure Swiper to use modules
    Swiper.use([Navigation]);

    let $this = this;

    export default {
        components: {
            Swiper,
            VuePlyr,
            ImageStory,
            AudioStory,
            VideoStory,
            HelpCenter
        },
        data() {
            return {
                storiesFormated: [],
                showHamburger: false,
                stickyHeader: false,
                mySwiper: null,
                swiperOptions: {
                    loop: false,
                    direction: 'horizontal',
                    // loop: true,
                    //loopedSlides: 5, // looped slides should be the same
                    slidesPerView: 9,
                    // slidesPerGroup: 5,
                    spaceBetween: 20,
                    slideToClickedSlide: true,
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev',
                    },
                    centeredSlides: true,
                    breakpoints: {
                        // when window width is >= 320px
                        320: {
                            slidesPerView: 2.3,
                            spaceBetween: 0
                        },
                        500: {
                            slidesPerView: 2.6,
                        },
                        // 550: {
                        //     slidesPerView: 2.8,
                        // },
                        600: {
                            slidesPerView: 3,
                        },
                        700: {
                            slidesPerView: 4,
                        },
                        800: {
                            slidesPerView: 5,
                        },
                        1000: {
                            slidesPerView: 6,
                        },
                        1200: {
                            slidesPerView: 7,
                        },
                        1300: {
                            slidesPerView: 8,
                        },
                        1600: {
                            slidesPerView: 9,
                        },
                        1900: {
                            slidesPerView: 10,
                        },

                    },
                }
            }
        },
        computed: {
            ...mapGetters({
                token: 'auth/token',
                memorial: 'story/memorial',
                isMemorialExist: 'story/isMemorialExist',
                stories: 'story/getStories',
            }),
        },
        methods: {
            openHelp() {
              EventBus.$emit('help-center-open');
            },
            async selectedRelative(relative) {
                await this.$store.dispatch('story/getRelativeStories', {
                    memorialId: this.memorial.id,
                    relativeId: relative.id
                })

                this.storiesFormated = Object.assign([], this.stories)

                for(let i in this.storiesFormated) {
                    this.storiesFormated[i].createdAtFormat = this.$dateUtils.formatDate(new Date(this.storiesFormated[i].createdAt), 'dd MMM yyyy', '-', this.$ml.current);
                }
            },
            handleScroll() {
                this.stickyHeader = window.scrollY > 100;
            },
            getMemorialPhoto() {
                return this.memorial.profilePhoto ? this.memorial.profilePhoto.url : require('@/assets/img/reserve-bg.png');
            },
            nextSlide() {
                let index = this.mySwiper.realIndex + 4;
                this.mySwiper.slideTo(index);

                if(this.memorial.relations[index] !== undefined) {
                    this.selectedRelative(this.memorial.relations[index].user);
                }
            },
            prevSlide() {
                let index = this.mySwiper.realIndex - 4;
                this.mySwiper.slideTo(index);

                if(this.memorial.relations[index] !== undefined) {
                    this.selectedRelative(this.memorial.relations[index].user);
                }
            }
        },
        beforeMount() {
            window.addEventListener('scroll', this.handleScroll);
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },
        async mounted() {
            await this.$store.commit('story/setMemorial', null);
            await this.$store.commit('story/setMemorialExist', false);
            await this.$store.commit('story/saveStories', []);

            if (!this.token) {
                await this.$store.dispatch('auth/getToken');
            }

            await this.$store.dispatch('story/getMemorial', this.$route.params.slug);

            if (!this.isMemorialExist) {
                return this.$router.push({name: 'home'});
            }

            // let $this = this;

            this.mySwiper = new Swiper('.swiper-container', this.swiperOptions)

            // this.mySwiper.on('slideChange', function(swiper) {
            //     $this.$store.commit('story/saveStories', []);
            //
            //     if($this.memorial.relations[swiper.realIndex] !== undefined) {
            //         $this.selectedRelative($this.memorial.relations[swiper.realIndex].user);
            //     }
            // });

            if(this.memorial.relations.length == 1 && !this.storiesFormated.length) {
                this.mySwiper.slideTo(4);
            }
            else if(typeof this.memorial.relations[0].user !== "undefined") {
                await this.selectedRelative(this.memorial.relations[0].user);

                if (this.$route.params.relative) {
                    for (let i in this.memorial.relations) {
                        if (this.memorial.relations[i].user.id == this.$route.params.relative) {
                            this.mySwiper.slideTo(i);
                            await this.selectedRelative(this.memorial.relations[i].user);
                            break;
                        }
                    }
                }
            }
        }
    }
</script>

<style style lang="scss">
    @import "~@/assets/scss/pages/relatives-stories.scss";
</style>