<template>
    <!-- qoute qoute--audio-player -->
    <div :class="['qoute qoute--audio-player', ...addClass]" v-if="item.file">
        <div class="qoute__img-box">
            <div class="qoute__img">
                <div class="qoute__img-wrapper">
                    <router-link v-if="memorialSlug" :to="{ name: 'memorials.relatives', params: { slug: memorialSlug, relative: owner.user.id } }">
                        <img :src="owner.user.profilePhoto ? owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')"
                             :alt="owner.user.name">
                    </router-link>
                    <img v-else :src="owner.user.profilePhoto ? owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')"
                         :alt="owner.user.name">
                </div>
                <div class="qoute__name">{{ owner.user.name }}</div>
                <div class="qoute__relations">{{ owner.relationToMemorialPerson }}</div>
            </div>
        </div>

        <div :class="['qoute__audio-player', ...addClassPlayer]">
            <VuePlyr>
                <audio class="qoute__audio-flowplayer" controls style="height: 100% ;">
                    <source type="audio/mp3" :src="item.file.url">
                </audio>
            </VuePlyr>
        </div>
    </div>
</template>

<script>
    import VuePlyr from 'vue-plyr';
    import 'vue-plyr/dist/vue-plyr.css';

    export default {
        name: "AudioStory",
        props: [
            'addClass', 'item', 'owner', 'memorialSlug', 'addClassPlayer'
        ],
        components: {
            VuePlyr
        },
        async mounted() {
            const settingButtons = await document.getElementsByClassName('plyr__menu');
            const settingButton = settingButtons[0];

            if (typeof settingButton !== 'undefined') {
                settingButton.innerHTML = '';
                settingButton.insertAdjacentHTML('afterbegin', `
                  <span class="audio-player__menu" id="audio-player-menu">
                    <svg style="width: 24px; height: 24px; padding: 0 10px 0 10px" xmlns="http://www.w3.org/2000/svg" focusable="false" width="4" height="16" viewBox="0 0 4 16">
                      <path id="Path_2017" data-name="Path 2017" d="M12,8a2,2,0,1,0-2-2A2.006,2.006,0,0,0,12,8Zm0,2a2,2,0,1,0,2,2A2.006,2.006,0,0,0,12,10Zm0,6a2,2,0,1,0,2,2A2.006,2.006,0,0,0,12,16Z" transform="translate(-10 -4)" fill="#4f91b3"/>
                    </svg>
                  </span>
              `);
            }
        }
    }
</script>