<template>
    <div v-if="item.file || item.link">
        <div :class="addClass" :style="{maxWidth: item.type === 'link' && checkIsYouTubeLink(item.link) ? '100%' : 'fit-content'}">
            <div class="image-mask"></div>

            <img v-if="item.contentType === 'image'" :src="item.file.url" alt="">

            <a v-else-if="item.type === 'link' && !checkIsYouTubeLink(item.link)" :href="item.link" target="_blank">
                <img v-if="item.previewImgLink" :src="item.previewImgLink" alt="Preview image">
            </a>

            <iframe
                v-else-if="item.type === 'link' && checkIsYouTubeLink(item.link)"
                width="100%" height="721px"
                :src="`https://www.youtube.com/embed/${getVideoId(item.link)}`"
                frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen
            ></iframe>
        </div>
    </div>
</template>

<script>
import { isYouTubeLink, parseVideoId } from '@/utils/YouTubeHelper';
    export default {
        name: "ImageStory",
        props: [
            'item', 'addClass'
        ],
        methods: {
          checkIsYouTubeLink(link) {
            return isYouTubeLink(link);
          },
          getVideoId(link) {
            return parseVideoId(link);
          },
        },
    }
</script>