<template>
    <div :class="['life-period__video-player', ...addClass]" style="margin-bottom: 70px;" v-if="item.file">
        <video-player :options="videoOptions" :addClass="'flow life-period__video-flowplayer'" :id="'vp-'+item.file.id"/>
    </div>
</template>

<script>
    import VideoPlayer from "@/components/VideoPlayer.vue";

    export default {
        name: "VideoStory",
        props: [
            'addClass', 'item'
        ],
        components: {
            VideoPlayer
        },
        data() {
            return {
                videoOptions: {
                    autoplay: false,
                    controls: true,
                    sources: [
                        {
                            src: null,
                        }
                    ]
                }
            }
        },
        created() {
            if(this.item.file) {
                this.videoOptions.sources[0].src = this.item.file.url;
            }
        },
    }
</script>

<style lang="scss">
    .video-js {
        max-width: 1088px;
        height: 721px;
        width: 100%;

        @media (max-width: 1199.98px) {
            max-height: 721px;
        }
    }
</style>